.registry {
    display: flex;
    grid-gap: 10px;

    .registry-image {
        width: 50%;
        border: 1px ridge goldenrod;
        border-radius: 8px;
        height: 100%;
        min-height: 85vh;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../../../assets/img/image-registry.jpg");

    }  

    a {
        text-decoration: none;
    }

    .registry-text {
        width: 50%;
        height: 100%;
        margin: auto;
        border-radius: 8px;
        padding: 40px;
        align-items: center;

        .registry-text-icon {
            margin: 0 auto;
            width: 100%;
            color: goldenrod;
            font-size: 12px;
        }

        .registry-information {
            margin: 30px auto;
            border: 1px outset goldenrod;
            padding: 20px;
            border-radius: 8px;
            display: flex;
            grid-gap: 12px;
            justify-content: space-between;
            color: black;
            font-size: 24px;
            max-width: 600px;
            transition: .25s ease-in-out all;

            &:hover, &:focus {
                color: black;
                cursor: pointer;
                background-color: rgb(236, 236, 236);
                transform: scale(1.01);
            }
        }
    }

    @media (max-width: 928px) {
        display: block;
        .registry-image {
            min-height: 200px;
            padding: 20px;
            width: 100%;
        }
        .registry-text {
            width: 100%;
            padding: unset;
        }
    }

}

